@import url("https://fonts.googleapis.com/css2?family=PT+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Hind+Siliguri&display=swap");
* {
  margin: 0;
  padding: 0;
}
body {
  overflow-x: hidden;
}
$theme: #182a30;
$color: #54defe;
img.left,
img.right {
  opacity: 0.15;
  width: 400px;
  height: 400px;
  z-index: -1000000000;
  position: fixed;
  top: 200px;
}
img.left {
  left: -200px;
}
img.right {
  right: -200px;
}
div.socials {
  position: fixed;
  right: 0;
  top: 65%;
  background: $color;
  width: 50px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  opacity: 0.5;
  z-index: 1;
  &:hover {
    opacity: 1;
  }
  .tooltip {
    position: relative;
    display: inline-block;
    .niche {
      transform: translateY(14px);
    }
  }
  .tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: $theme;
    color: $color;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    top: -5px;
    right: 110%;
  }

  .tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 100%;
    margin-top: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent transparent $theme;
  }
  .tooltip:hover .tooltiptext {
    visibility: visible;
  }
  a {
    text-decoration: none;
    i {
      text-align: center;
      cursor: pointer;
    }
    i.fa-facebook {
      display: block;
      color: $theme;
      font-size: 25px;
      padding: 10px;
    }
    i.fa-twitter {
      color: #54defe;
      background: #182a30;
      border-radius: 50%;
      margin-left: 12px;
      font-size: 15px;
      padding: 6px;
      margin-bottom: 10px;
    }
    i.fa-youtube {
      background: $theme;
      color: $color;
      border-radius: 50%;
      margin-left: 12px;
      font-size: 16px;
      padding: 5px;
      margin-bottom: 10px;
    }
    i.fa-instagram {
      color: $color;
      background: $theme;
      border-radius: 50%;
      margin-left: 12px;
      font-size: 15px;
      padding: 8px;
      margin-bottom: 10px;
    }
  }
}
section {
  z-index: 1;
}
::-webkit-scrollbar {
  width: 8px;
  background: $theme;
}
::-webkit-scrollbar-thumb {
  background: $color;
  border-radius: 6px;
}
@media (max-width: 340px) {
  img.right {
    display: none;
  }
  img.left {
    display: flex;
    justify-content: center;
    left: 0;
    width: 300px;
    height: 300px;
  }
}
